.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 90px 0;
}

.card {
  margin: 0 3% !important;
}

.infected {
  border-bottom: 10px solid rgba(0, 0, 255);
  width: 400px;
}

.recovered{
  border-bottom: 10px solid rgb(0, 255, 0);
  width: 400px;

}

.deaths{
  border-bottom: 10px solid rgb(255, 0, 0);
  width: 400px;

}

@media only screen and (max-width: 770px) {
  .card {
    margin: 2% 0 !important;
  }
}